        Tool_Event.instance.On_Event_TOUCH_END(this.VM_BtnSetting.getChildByName("Icon"), FunVMBtnSetting);



        Tool_Event.instance.On_Event_TOUCH_END(this.VM_BtnFlip.getChildByName("Btn_L"), FunVMBtnFlipL);
        Tool_Event.instance.On_Event_TOUCH_END(this.VM_BtnFlip.getChildByName("Btn_R"), FunVMBtnFlipR);


        Tool_Event.instance.On_Event_TOUCH_END(this.VM_FootPrints.getChildByName("Icon"), FunVMFootPrints);
        Tool_Event.instance.On_Event_TOUCH_END(this.VM_BtnRegion.getChildByName("Icon"), FunVMBtnRegion);
