    /** */
    OnPanelMore(AnimSwitch: boolean = false) {
        /** */
        const FunPMBtnSetting = () => {
            this.Resoure_Data.Sound_Play("Audio/SoundBtn");
            this.UpdatePanelSetting(true);
        }
        /** */
        Tool_Event.instance.On_Event_TOUCH_END(this.PM_BtnSetting.getChildByName("Icon"), FunPMBtnSetting);
        /** */
        Tool_Event.instance.Set_Btn_Event_Anim(this.PM_BtnSetting.getChildByName("Icon"), AnimSwitch);
        let Window = find("Window", this.PanelMore);
        for (let i = 0; i < Window.children.length; i++) {
            Tool_Event.instance.On_Event_TOUCH_END(Window.children[i].getChildByName("Icon"), async () => {
                switch (Window.children[i].name) {
                    case "ThemeIdiom":
                        this.Resoure_Data.Sound_Play("Audio/SoundBtn");
                        if (this.PanelIdiom == null) {
                            /** */
                            await this.LoadPanelIdiom();
                        }
                        this.PanelIdiom.active = true;
                        // this.IdiomNext();
                        break;

                    default:
                        break;
                }
            });
        }
    }

