            //     let PanelRegionRanking: Node = Tool_UI.instance.Set_Prefab_Node(PrefabPanelRegionRanking, this.VM_Content, v3(0, 0, 0), this.ConfigRegionRanking[Sort[i]].Id + "");
            //     Tool_UI.instance.Set_UIOpacity(PanelRegionRanking, 254);
