    /** */
    LoadViewNormalScene(): Promise<void> {
        return new Promise(async (resolve, reject) => {
            let Map = find("ScrollView/view/VNContent/Map", this.ViewNormal)
            /** */
            let PrefabPanelMap = await this.Resoure_Data.Load_Prefab("Prefab/PanelMap");
            this.VN_Map = Tool_UI.instance.Set_Prefab_Node(PrefabPanelMap, Map, v3(0, 0, 0), "");
            /** */
            let PrefabRegionDish = await this.Resoure_Data.Load_Prefab("Prefab/RegionDish");
            /** */
            let Sort = this.SortRegionDish;
            //
            for (let i = 0; i < this.NumberOfProvinces; i++) {
                let RegionDish: Node = Tool_UI.instance.Set_Prefab_Node(PrefabRegionDish, this.VN_Content, v3(0, 0, 0), this.ConfigRegionDish[Sort[i]].Id + "");
                /** */
                Tool_Event.instance.On_Event_TOUCH_START(RegionDish, () => {
                    this.IsMove = false;
                });
                Tool_Event.instance.On_Event_TOUCH_MOVE(RegionDish, () => {
                    this.IsMove = true;
                });
                Tool_Event.instance.On_Event_TOUCH_END(RegionDish, async () => {
                    if (this.IsMove) {
                        return;
                    }
                    const func = async () => {
                        this.Resoure_Data.Game_Mode = Game_Mode.Normal;
                        this.TopicDataId = Number(RegionDish.name);
                        super.GetTopicData();
                        if (!this.TopicData.length) {
                            this.OnMessage("!");
                            return;
                        }
                        this.ViewNormal.active = false;
                        this.ViewMain.active = false;
                        /** */
                        Tool_Other.instance.Sort_Random(this.TopicData);
                        /** */
                        this.TopicCount = 10//this.TopicData.length;
                        this.TopicIndex = 0;
                        this.IsHint = false;
                        this.IsContinue = false;
                        this.LastPlayId = this.ConfigRegionDish[this.TopicDataId].Id - 1;
                        /** */
                        this.UpdateScene();
                        /** */
                        super.DataSet();
                        this.PanelGameShow();

                    }
                    let IsFree: boolean = this.ConfigRegionDish[Sort[i]].IsFree;
                    if (IsFree) {
                        func();
                    } else {
                        PlatformApi.instance.showVideoAd(() => {
                            func();
                        }, () => {
                            return;
                        });
                    }

                });
                /** */
                Tool_Event.instance.Set_Btn_Event_Anim(RegionDish, this.AnimSwitch);
            }
            Tool_Event.instance.On_Event_TOUCH_MOVE(this.VN_Content, () => {
                this.IsMove = true;
            });
            resolve();
        });
    }

