    /** */
    UpdatePanelSidebar() {
        console.warn(this.SidebarState, this.SidebarState === ConfigSidebarState.Ok);
        this.PD_BtnClose.active = true;
        switch (this.SidebarState) {
            case ConfigSidebarState.Ok:
                this.PD_BtnOk.active = false;
                this.PD_BtnGet.active = false;
                break;
            case ConfigSidebarState.Get:
                this.PD_BtnOk.active = false;
                this.PD_BtnGet.active = true;
                break;
            case ConfigSidebarState.Other:
                this.PD_BtnClose.active = false;
                this.PD_BtnGet.active = false;
                this.PD_BtnOk.active = true;
                break;
            default:
                break;
        }
    }

